//------------------------------
//変数
//------------------------------
//色
$color_text: #4C4948;
//メディアクエリ
$sp:  "only screen and (max-width:750px)";
$pc:  "only screen and (min-width:751px)";

@media #{$sp} {

  .pc {
    display: none;
  }
  .sp {
    display: block;
  }

  img.pc {
    display: none;
  }
  img.sp, span.sp {
    display: inline-block;
  }


}

@media screen and (min-width:751px) {
  .pc {
    display: block;
  }
  .sp {
    display: none;
  }
  img.pc, span.pc {
    display: inline-block;
  }
  img.sp {
    display: none;
  }
}
