//---------------------------
// footerのスタイル
//---------------------------
.l-footer {
  background-color: #EDECE1;
  padding: 90px 0 20px;
  @media #{$sp}{
    padding: 35px 0 20px;
  }
  .l-inner{
    display: flex;
    @media #{$sp}{
      width: calc(100% - 15%);
      flex-direction: column-reverse;
    }
  }
  &__maparea{
    width: 405px;
    height: 305px;
    @media #{$sp}{
      width: 100%;
      height: 205px;
      margin-top: 32px;
    }

  }
  &__textarea{
    margin: 22px 40px 0;
    @media #{$sp}{
      flex-direction: column-reverse;
      margin: 0 auto;
    }
  }
  &__name{
    font-size: 20px;
    letter-spacing: 5.4px;
    @media #{$sp}{
      font-size: 18px;
      letter-spacing: 3px;
    }
  }
  &__address{
    margin-top: 28px;
    @media #{$sp}{
      margin-top: 6px;
      font-size: 13px;
    }
  }
  &__contact{
    margin-top: 8px;
    @media #{$sp}{
      font-size: 13px;
    }
  }
  &__mail{
    margin-left: 20px;
    font-size: 16px;
    letter-spacing: 3px;
    @media #{$sp}{
      font-size: 13px;
    }
  }
  &__date{
    margin-top: 6px;
    line-height: 2.5;
    font-size: 16px;
    letter-spacing: 3px;
    @media #{$sp}{
      line-height: normal;
      font-size: 13px;
    }

  }
  &__sns{
    margin-top: 18px;
    display: flex;
    letter-spacing: 3px;
    li{
      margin-left: 28px;
      @media #{$sp}{
        margin-left: 15px;
      }
      &:first-child{
        margin-left: 0;
      }
    }
    @media #{$sp}{
      margin-top: 6px;
      font-size: 13px;
      letter-spacing: 3px;
    }
  }
  &-copyrigmht{
    margin-top: 74px;
    text-align: center;
    @media #{$sp}{
      font-size: 12px;
      margin-top: 20px;
    }
  }
}
