//-------------------------------------
// TOPページのスタイル
//-------------------------------------
.l-content{
  padding-top: 205px;
  @media #{$sp}{
    padding-top: 0;
    margin-top: 108px;
  }
}
.instaArea__img{
  display: flex;
  justify-content: space-between;
  height: auto;
  a{
    display: block;
    flex: 0 0 45%;
    width: 100%;
    height: auto;
    @media #{$sp}{

    }
    &:last-child{
      padding-top: 160px;
      @media #{$sp}{
        padding-top: 55px;
      }
    }
  }
  img{
    width: 100%;
    height: auto;
    transition: 1s ease;
    @media #{$sp}{
      margin: 0;
    }
    &:hover{
      transform: scale(1.05);
    }
  }
  .touchimg.on{
    transform: scale(1.1)
  }
  &02{
    flex-direction: row-reverse;
  }
}


.content__img{
  margin: 0 0 106px;
  @media #{$sp}{
    margin: 0 0 36px;
  }
}
.content__img02{
  margin-top: 106px;
  @media #{$sp}{
    margin-top: 36px;
    img{
      width: 75%;
    }
  }
}
.content__img03{
  margin: 106px 0;
  text-align: right;
  @media #{$sp}{
    margin: 36px 0;
    img{
      width: 75%;
    }
  }
}
.content__img04{
  @media #{$sp}{
    margin: 0 0 60px;
  }
}
.textarea{
  margin-top: 106px;
  padding: 78px 145px;
  background-color: #EDECE1;
  @media #{$sp}{
    padding: 36px;
    margin: 36px 0;
  }
  &__desc{
    text-align: right;
  }
}

.news-area{
  &__ttl{
    @media #{$sp}{
      height: 8px;
      width: auto;
      img{
        height: 100%;
      }
    }
  }
  position: relative;
  @media #{$sp}{
    &__list{
      a{
        display: flex;
        flex-direction: column;
        margin-top: 10px;
      }
    }
  }
  &__date{
    margin-top: 20px;
    display: inline-block;
    width: 160px;
    @media #{$sp}{
      width: 100%;
      font-size: 14px;
      margin-top: 15px;
      &:first-child{
        margin-top: 0;
      }
    }
  }
  &__txt{
    margin-left: 20px;
    @media #{$sp}{
      margin-left: 0;
    }
  }
  &__btn{
    width: 124px;
    height: 60px;
    border: 2px solid #EDECE1;
    position: absolute;
    right: 0;
    @media #{$pc}{
      bottom: 0;
    }
    @media #{$sp}{
      width: 66px;
      height: 32px;
      top: -20px;
    }
    a{
      display: flex;
      justify-content: center;
      align-items: center;
      width: 100%;
      height: 100%;
      picture{
        display: flex;
        justify-content: center;
        align-items: center;
      }
      @media #{$sp}{
        img{
          width: 35px;
        }
      }
    }
  }
}

.onlineShop-area{
  &__ttl{
    @media #{$sp}{
      width: 100px;
      height: auto;
    }
  }
  margin: 150px 0 167px;
  @media #{$sp}{
    margin: 45px 0 55px;
  }
  &__item{
    margin: 23px 0 0;
    display: flex;
    justify-content: space-between;
    @media #{$sp}{
      margin: -10px 0 0;
    }
    img{
      transition: 1s ease;
      &:hover{
        transform: scale(1.05);
      }
    }
    @media #{$sp}{
      flex-direction: column;
      a{
        display: flex;
        width: 100%;
        height: 100%;
        margin-top: 20px;
        img{
          width: 33%;
          height: 100%;
        }
      }
    }
  }
  &__name{
    margin-top: 14px;
    text-indent: -10px;
    position: relative;
    @media #{$sp}{
      margin-top: 0;
      text-indent: 0;
      margin-left: 10px;
    }
  }
  &__price{
    position: absolute;
    right: 35px;
    @media #{$sp}{
      position: static;
      display: block;
    }
  }
  &__btn{
    margin: 66px auto 0;
    border: 2px solid #EDECE1;
    width: 344px;
    height: 60px;
    @media #{$sp}{
      margin: 20px auto 0;
      width: 190px;
      height: 32px;
    }
    a{
      display: flex;
      justify-content: center;
      align-items: center;
      width: 100%;
      height: 100%;
      img{
        display: flex;
        justify-content: center;
        align-items: center;
        @media #{$sp}{
          width: 95px;
        }
      }
    }
  }
}

.fade-in{
  transition: opacity 0.9s;
}
