/*--------------------------------------------------------------------------
 *	 html, body;
 *---------------------------------------------------------------------------*/
html {
 font-size: 62.5%
}
html, body {
  margin: 0;
  padding: 0;
  font-family:"A1明朝","游明朝","ヒラギノ明朝 Pro W3", "さわらび明朝",serif;
  color: $color_text;
}
body {
  font-size: 15px;
  letter-spacing: 1.2px;
  line-height: 1.87;
  @media #{$pc}{
    min-width: 1000px;
  }
}

/*--------------------------------------------------------------------------
 *	 base link;
 *---------------------------------------------------------------------------*/
a {
  color: $color_text;
  outline: none;
  opacity: 1;
  -webkit-tap-highlight-color: transparent;
  -webkit-transition-duration: 0.1s;
  transition-duration: 0.1s;
  &:link, &:visited{
    text-decoration: none;
  }
  &:hover{
    text-decoration: none;
    outline: none;
    opacity: 0.7;
  }
}

img {
  max-width: 100%;
  height: auto;
}

/* clear fix */
.cf{
  &:before{
    display: table;
    content: '';
  }
  &:after{
    display: table;
    content: '';
    clear: both;
  }
  zoom: 1;
}
