//---------------------------
// headerのスタイル
//---------------------------
.l-header {
  position: relative;
  z-index: 700;
  &__mv{
    animation: fadeIn 3s ease 0s 1 normal;
    @keyframes fadeIn {
      0% {opacity: 0}
      100% {opacity: 1}
    }
    position: relative;
    .slidetoggle_button-w{
      span{
        position: absolute;
        right: 20px;
        top: 20px;
        &:before{
          content: "";
          display: block;
          height: 1px;
          width: 20px;
          background-color: #fff;
          margin: 8px auto;
        }
        &:after{
          content: "";
          display: block;
          height: 1px;
          width: 20px;
          background-color: #fff;
        }
      }
    }
    img{
      width: 100%;
    }
  }
  &__img{
    height: 100vh;
    li{
      position: relative;
      width: 100vw;
      height: 100vh;
      background-size: cover;
      background-position: center center;
      &:nth-child(1){
        background-image: url(images/slider/slider_01.jpg);
        @media #{$sp}{
          background-image: url(images/slider/slider_01_sp.jpg);
        }
      }
      &:nth-child(2){
        background-image: url(images/slider/slider_02.jpg);
        @media #{$sp}{
          background-image: url(images/slider/slider_02_sp.jpg);
        }

      }
      &:nth-child(3){
        background-image: url(images/slider/slider_03.jpg);
        @media #{$sp}{
          background-image: url(images/slider/slider_03_sp.jpg);
        }

      }
      &:nth-child(4){
        background-image: url(images/slider/slider_04.jpg);
        @media #{$sp}{
          background-image: url(images/slider/slider_04_sp.jpg);
        }
      }
    }
  }
  &__logo{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  .scroll-btn{
    width: 30px;
    position: absolute;
    transform: translateX(-50%);
    left: 50%;
    bottom: 20px;
  }

  .nav-area{
    width: 100%;
    z-index: 999;
    position: absolute;
    background-color: #EDECE1;
    &.follow{
      position: fixed;
      top: 0;
    }
  }
  .nav-area__wrap{
    display: flex;
    justify-content: space-between;
    padding: 30px 0;
    box-sizing: border-box;
    width: 1130px;
    margin: 0 auto;
    @media #{$sp}{
      width: 100%;
      padding: 15px 22px 15px 45px;
    }
    &__logo{
      margin-top: 5px;
      @media #{$sp}{
        width: 100px;
        height: auto;
      }
    }
    .slidetoggle_button{
      span{
        position: absolute;
        right: 20px;
        &:before{
          content: "";
          display: block;
          height: 1px;
          width: 20px;
          background-color: #000;
          margin: 8px auto;
        }
        &:after{
          content: "";
          display: block;
          height: 1px;
          width: 20px;
          background-color: #000;
        }
      }
    }


    &__nav{
      display: flex;
      align-items: center;
      @media #{$sp}{
        display: none;
        margin-top: 25px;
      }
    }
    li{
      margin-left: 50px;
      @media #{$sp}{
        margin-left: 0;
        margin-right: 20px;
      }
      a{
        letter-spacing: 2.6px;
        font-size: 17px
      }
    }
  }
  .sp-menu {
    display: none;
    position: fixed;
    width: 100vw;
    height: 100vh;
    background-color: #EDECE1;
    z-index: 9990;
    top: 0;
    .close_button{
      span:before {
        width: 20px;
        height: 1px;
        content: "";
        background-color: #000;
        display: block;
        transform: translateY(-20px) rotate(45deg);
        position: fixed;
        top: 50px;
        right: 20px;
      }
      span:after {
        content: "";
        width: 20px;
        height: 1px;
        background-color: #000;
        display: block;
        transform: translateY(20px) rotate(-45deg);
        position: fixed;
        top: 10px;
        right: 20px;
      }
    }
    &__logo{
      padding-top: 50px;
      text-align: center;
      width: 220px;
      margin: auto;
    }
    ul{
      padding: 0 90px ;
    }
    &__nav{
      margin-top: 65px;
      a{
        font-size: 24px;
        line-height: 2.4;
      }
    }
    &__sns{
      margin-top: 20px;
      a{
        line-height: 3;
      }
    }
  }

}
